.wrapperFilter {
  width: 300px;
  height: 40px;
  display: flex;
  align-items: center;
  border: none;
  padding: 10px;
  border-radius: 5px;
  background-color: #d7d4f7;
  box-shadow: 6px 6px 9px -1px rgba(0, 0, 0, 0.47);
  -webkit-box-shadow: 6px 6px 9px -1px rgba(0, 0, 0, 0.47);
  -moz-box-shadow: 6px 6px 9px -1px rgba(0, 0, 0, 0.47);
  margin-bottom: 20px;
}

.input {
  border-radius: 5px;
  background-color: #f4f7d4;
  border: 1px solid #4f09d1;
  padding: 3px;
}

.input:hover,
.input:focus {
  border: 1px solid red;
}

.label {
  margin-bottom: 5px;
  text-align: center;
}
